*{
  margin: 0;
  padding: 0;
}

body{
  background-color: rgb(32, 32, 197);
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.passwordBox{
  width: 350px;
  /* height: 300px; */
  padding: 20px;
  background-color: violet;
}

.passwordBoxIn{
  display: flex;
  
}
.passwordBoxIn input{
  flex-basis: 80%;
  padding-left: 5px;
}
.passwordBoxIn button{
  flex-basis: 20%;
  padding: 10px 0px;
}
.passLength{
  padding: 10px 0px;
  display: flex;
  align-items: center;
  justify-content:space-between;
}
.passLength input{
  padding: 10px 0px;
  width: 65px;
}

.btn{
  width: 100%;
  padding: 10px 0px ;
  cursor: pointer;
}